*{
  padding:0;
  margin:0;
  box-sizing:border-box;
}
:root{
  --white: #fff;
  --black: #000;
  --lightBlack:#2c2c2c;
  --main:#ff6e68;
}
body{
  font-family: 'Open Sans',sans-serif;
}
.container{
  width: 1300px;
  margin:0 auto;
}
.nav{
  width: 100%;
  height: 70px;
  box-shadow: 0 5px 20px 2px rgba(0,0,0,1);
  position: fixed;
  top:0;
  background:var(--white);
  z-index:1
}
.nav__conatiner{
  width: 100%;
  display:flex;
  align-items:center;
  height:70px;
  justify-content: space-between;
}
.nav__left img{
  width:100px;
}
.header{
  background: url("./images/banner.webp");
  width: 100%;
  height: 100vh;
}
a{
  text-decoration: none;
}
.cart-icon{
  color: var(--main);
  font-size: 22px;
}
.basket{
  position:relative;
}
.basket span{
  display:flex;
  align-items: center;
  justify-content:center;
  position: absolute;
  right: -15px;
  top:-10px;
  width: 25px;
  height: 25px;
  background:var(--lightBlack);
  border-radius:50%;
  color:var(--white);
  font-size: 12px;
}
.row{
  display: flex;
  width: 100%;
  flex-wrap:wrap;
}
.col-2{
  width: 16.33333333333333%;
}
.col-3{
  width:25%
}
.col-6{
  width: 50%;
}
.col-9{
  width: 75%;
}
.mt-100{
  margin-top:100px;
}
.product{
  padding: 25px;
  width: 100%;
  overflow: hidden;
}
.product__img{
  width: 100%;
  height: 350px;
  overflow: hidden;
}
.product__img img{
   width: 100%;
  height: 100%;
  transition: all .5s linear;
}
.product__img img:hover{
  transform: scale(1.2);
}
.product__name{
  margin: 10px 0;
  text-transform: capitalize;
  color: #707070;
}
.product__discount__price{
  text-align:right;
  font-size: 18px;
}
.product__price .discount{
  color: silver;
  font-weight: 400;
  font-size: 13px;
}
.actualprice{
  text-decoration: line-through;
  font-size:18px;
}
.details__image{
  width:100%;
  height:auto;
  padding-right:30px;
}
.details__image img{
  width:100%;
  height:auto;
  margin-bottom:25px;
}
.details__name{
  font-size:28px;
  color:#2c2c2c;
  font-weight:400;
  text-transform: capitalize;
}
.details__price{
  margin:10px 0;
   
}
.details__actual{
  font-size:20px;
  text-decoration: line-through;
}
.details__discount{
  margin-left:20px;
  font-size:20px;
}
.details__incDec{
    display:flex;
}
.dec,.inc,.quantity{
  width:60px;
  height:60px;
  border:1px solid silver;
  display:flex;
    align-items: center;
  justify-content: center;
}
.quantity{
  border-left:none;
  border-right:none;
}
.inc,.dec{
  cursor: pointer;
}
.btn-default{
  margin-left:10px;
  width:100%;
  border:none;
  background:var(--main);
  color:white;
  outline:none;
  font-size:18px;
  text-transform: uppercase;
  cursor: pointer;
}

.btn-default:focus{
  outline: none;
}
.details__p{
  margin-top:15px;
}
.details__p h4{
  font-weight:600;
  margin-bottom:10px;
  font-size: 18px;

}
/* cart */
.cart{
  margin-top:100px;
}
.cart h3{
  font-weight: 400;
}
.cart__heading{
  background: #f4f6f8;
  padding: 10px;
}
.cart__image img{
  width:100px;
  height:100px;
  object-fit:contain;
}
.verticalAlign{
  align-items: center;
  margin-top:10px;
}
.text-center{
  text-align:center;
}
.cart__remove{
  cursor:pointer;
}
.summary{
  padding-left:10px;
}
.summary__heading{
  background: #f4f6f8;
  padding:10px;
}
.summary__details{
  background: var(--main);
  color:var(--white);
  padding:15px;
}
.mb-10{
  margin-bottom:10px;
}
@media (max-width: 765px){
  .container{
    width: 100%;
    padding: 0 30px;
  }
  .col-3{
    width:50%;
  }
  .col-2{
    width:100%;
  }
  .col-6{
    width:100%;
  }
  .details__image{
    padding-right:0px;
}
.cart__heading{
  display:none
}
}
@media (max-width: 400px){
  .col-3{
    width:100%;
  }
}
